body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url(/static/media/dawn.059715b3.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50% 35%;
}

.Game-board {
  text-align: center;
  font-family: Georgia;
}

.Game-header {
  color: white;
  background: #293b6d;
  height: 50px;
  padding: 20px;
}

.Game-title {
  font-size: 1.5em;
}

.Game-status {
  padding: 20px; 
}

.Game-status div {
  color: #fff;
  margin-bottom: 10px; 
}

.Card {
  display: inline-block;
  border-color: #fff;
  border-width: 5px;
  border-style: solid;
  margin-bottom: 10px;      
  margin-left: 10px;  
  width: 100px;
  border-radius: 10px;    
}

.Matched {
  border-color: #B084CC;
}

.Card-container {
  display: inline-block;
  max-width: 550px;
  margin: 20px auto;
}

.Lvl-button button {
  padding: 3px 15px 3px 15px; 
  margin-right: 20px;
  margin-bottom: 10px;
  border-color: #fff;
  border-width: 3px;
  color: #fff;
  background: #293b6d;
  cursor: pointer;
  border-radius: 10px;
} 

.Lvl-button button:hover {
  font-weight: bold;
}
